<template>
  <div id="wcard" v-if="info">
    <b-card>
      <b-card-text>
        <div class="mb-3" style="font-family: 'Titan One', monospace; font-size: 20px; color: #2b3752">
          {{ info.name }}
        </div>
        <div>{{ days }}</div>
        <div>
          <img
            style="display: inline-block; width: 50px; height: 50px"
            :src="require(`@/assets/weather/${info.weather}.gif`)"
            alt=""
          />
          <h2 class="m-2" style="display: inline-block; font-size: 23px">{{ info.temp }}</h2>
          <!--  -->
          <div style="display: inline-block">{{ info.min_temp }} / {{ info.max_temp }}</div>
          <!-- / -->
        </div>
        <div>
          <!-- : {{ info.temp }}  : {{ info.min_temp }}  : {{ info.max_temp }}  :
         -->
        </div>
      </b-card-text>
    </b-card>
  </div>
</template>

<script>
import { mapState } from "vuex";
// let date = new Date();
// let month = date.getMonth() + 1;
// let day = date.getDate();
// document.getElementById("current_date").innerHTML = ", " + month + " " + day + "";
const mapStore = "mapStore";

export default {
  data() {
    return {
      days: "11 23",
    };
  },
  created() {
    this.getDays();
  },
  computed: {
    ...mapState(mapStore, ["weatherLoc", "info", "apt"]),
  },

  methods: {
    getDays() {
      this.days = "";
      let date = new Date();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      this.days = ", " + month + " " + day + "";
    },
  },
};
</script>

